import React, { useEffect, useState } from 'react'
import Calender from '../../calendar/Calender'
import HolidayTable from './HolidayTable'
import { holidaysActions } from '../../../core/resource/actions'
import { useDispatch, useSelector } from 'react-redux'
import moment from 'moment'

const HolidayCalenderList = () => {
    const dispatch = useDispatch();
    const {
        getHolidayRes,
        getHolidayPending,
        getHolidayError,
    } = useSelector(state => state.holiday)
    const [allHolidayListData, setAllHolidayListData] = useState([])
    const [currentDate, setCurrentDate] = useState(moment());

    useEffect(() => {
        getAllHoliday()
    }, [])

    const getAllHoliday = () => {
        dispatch(holidaysActions.getHolidayRequest());
    }

    useEffect(() => {
        if (!getHolidayPending) {
            if (getHolidayError) {
                return;
            }
            if (getHolidayRes) {
                if (getHolidayRes?.code === 200) {
                    const getAllHolidayResData = getHolidayRes?.data
                    setAllHolidayListData(getAllHolidayResData)
                }
            }
        }
    }, [
        getHolidayRes,
        getHolidayError,
        getHolidayPending
    ]);
    const [currentPage, setCurrentPage] = useState(1)
    return (
        <>
            <Calender
                setCurrentDate={setCurrentDate}
                currentDate={currentDate}
                attendanceDetails={allHolidayListData}
                handleOpenClose={''}
                calenderType={"holiday"}
            />
            <HolidayTable
                currentPage={currentPage}
                setCurrentPage={setCurrentPage}
                allHolidayListData={allHolidayListData}
            />
        </>
    )
}

export default HolidayCalenderList
